	module.exports = {"data":{"markdownRemark":{"html":"<h1>Android Development</h1>\n<p>Android apps can be a great, fun way to get into the world of programming. Programmers primarily use Java to develop for Android, and the possibilities are endless.</p>\n<p>From simple games and utility apps to full-blown music players, there are many opportunities to create something meaningful with Android. The Android developer community is widespread, and the documentation and resources online are easy to find, so that you can tackle any issue you’re facing.</p>\n<p>There is definitely a learning curve to get used to the Android framework, however once you understand the core components that make up the app, the rest will come naturally.</p>\n<h2>Getting started</h2>\n<p>Check out the guides in this folder to learn about the 4 core components that make up an Android app and how you can get started with a sample app, and then delve into the more advanced topics such as fragments and the gradle build system. Then check out the material design specifications guide as well to learn how to make your apps beautiful and user friendly.</p>\n<p>Lastly, learn to integrate 3rd party libraries and Firebase services to add functionality to your app.</p>\n<h3>Official Documentation</h3>\n<p><a href=\"https://developer.android.com/training/index.html\">Google Developers Guide for Android</a></p>","fields":{"slug":"/android-development/"},"frontmatter":{"title":"Android Development"}}},"pathContext":{"slug":"/android-development/"}}

